<template>
    <div class="reservation">
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input :placeholder="$L('请输入搜索订单')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="search-status">
            <el-button size="small" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
            <el-select v-model="messageType" size="small"
                       @change="changeTypeFunc">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.name"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>
        <div class="list-content" v-loading="loading" v-if="tableData && tableData.hasOwnProperty('list') && tableData.list.length > 0">
            <div class="list-main" v-for="(item,key) in tableData.list">
                <div class="item head">
                    <div class="left">{{$L('订单号')}}：{{ item.orderSn }}</div>
                    <div v-if="item.order_status == 1" class="right font-red">{{$L('待处理')}}</div>
                    <div v-else-if="item.order_status == 2" class="right font-green">{{$L('已消费')}}</div>
                    <div v-else-if="item.order_status == 3" class="right">{{$L('已取消预约')}}</div>
                    <div v-else-if="item.order_status == 4 && item.comments_status === 0" class="right">{{$L('待评价')}}</div>
                    <div v-else-if="item.order_status == 4 && item.comments_status === 1" class="right">{{$L('已评价')}}</div>
                    <div v-else-if="item.order_status == 5" class="right">{{$L('已逾期')}}</div>
                    <div v-else class="right">{{$L('未知状态')}}</div>
                </div>
                <div class="item item-detail" @click="getInfoFunc(item.id)">
                    <div class="left">
                        <div class="info">
                            <div class="list-main-left">
                                <img class="img" :src="item.productPic" alt="">
                            </div>
                            <div class="list-main-right">
                                <p class="order-info">{{item.proName}}</p>
                                <div class="price">{{$L('预付')}}:{{ item.moneyPaid }}</div>
                            </div>

                        </div>
                    </div>
                    <div class="right">
                        {{ item.bookTime }}<br>
                        <span v-if="item.timeSpec">{{ item.timeSpec }}</span>
                    </div>
                </div>
                <div class="item item-btn">
                    <el-button type="primary" size="small" v-if="item.order_status === 1 && item.pay_status === 0" @click="changeOrderType(item,'pay')">{{$L('立即支付')}}</el-button>
                    <el-button type="default" size="small" v-if="item.order_status === 1"  @click="changeOrderType(item,'cancel')">{{$L('取消预约')}}</el-button>
                    <el-button type="default" size="small" v-else-if="item.order_status >= 3"  @click="changeOrderType(item,'del')">{{$L('删除订单')}}</el-button>
                    <el-button type="primary" size="small" v-if="item.order_status >= 3 && item.comments_status === 0"  @click="changeOrderType(item,'evaluate')">{{$L('去评价')}}</el-button>
                    <el-button type="primary" size="small" v-if="item.order_status >= 3 && item.comments_status === 1"  @click="changeOrderType(item,'evaluateInfo')">{{$L('查看评价')}}</el-button>
                </div>
            </div>
            <div class="page">
                <el-pagination
                        small
                        background
                        @current-change="handleCurrentChange"
                        layout="prev, pager, next"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
        <div class="tips" v-else v-loading="loading">
            <p class="no-list">{{ $L('无预约信息') }}</p>
        </div>
		<transition name="fade">
			<div class="time" v-if="showTime">
				<div class="close" @click="closeTimeFunc"></div>
				<div class="date">
					<calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
					<div class="btns">
						<el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
						<el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
					</div>
				</div>
			</div>
		</transition>
    </div>
</template>

<script>
    import orderPay from "../orderChildren/orderPay";
    export default {
        name: "reservation",
        components: {orderPay},
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
				showTime:false,
                timeArr:[],
                messageType:'all',
                messageTypelist:[
                    {
                        type:'all',
                        name:this.$L('全部时间')
                    },
                    {
                        type:'seven',
                        name:this.$L('近7天订单')
                    },
                    {
                        type:'thirty',
                        name:this.$L('近30天订单')
                    },
                    {
                        type:'halfYear',
                        name:this.$L('近半年订单')
                    },
                    {
                        type:'year',
                        name:this.$L('近1年订单')
                    },
                    {
                        type:'yearAgo',
                        name:this.$L('1年以前订单')
                    }
                ],
                currentPage:1,
                pageSize:10,
                dtCreateStart:'',
                dtCreateEnd:'',
                inputValue:'',
                tableData:{},
                searchType: false,
				defaultDate:[],
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.setupTopBtn();
            this.showSearch(false);
            this.getData(to);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        methods : {
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                if(!router){
                    router = _self.$route;
                };
                _self.loading = true;
                let arr = {
                    searchStatus : router.params.type ? router.params.type :'all',
                    p: _self.currentPage,
                    num: _self.pageSize,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd,
                    timeScreening: _self.messageType,
                    keyword: _self.inputValue
                }
                _self.$api('getReservationList', arr, function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            SearchTimeFunc(time) {
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = '';
                    this.dtCreateEnd = '';
                }
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val) {
                this.messageType = val;
                this.getData();
            },
            handleSizeChange(val){
                
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            changeOrderType(item, status) {
                switch (status) {
                    case 'pay':
                        //立即付款
                        if(item.order_status === 1 || item.pay_status === 1){
                            this.$store.commit('setDialogObj', {
                                name: this.$L('订单支付'),
                                view: orderPay,
                                width: '900px',
                                loading: true,
                                data:{
                                    orderId: item.orderId,
                                }
                            });
                        }
                        break;
                    case 'cancel':
                        //取消订单
                        if (item.order_status === 1) {
                            this.$confirm(this.$L('确认取消订单吗？'), this.$L('取消订单'), {
                                confirmButtonText: this.$L('立即取消'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('cancleReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'del':
                        //删除订单
                        if (item.order_status >= 3) {
                            this.$confirm(this.$L('是否确认删除订单？'), this.$L('删除订单'), {
                                confirmButtonText: this.$L('立即删除'),
                                cancelButtonText: this.$L('我再想想'),
                                type: 'warning',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                this.showLoad(true);
                                this.$api('delReservation', {
                                    orderId: item.orderId,
                                }, (reData) => {
                                    this.showLoad(false);
                                    if (reData && reData.flag === 1) {
                                        this.$message.success(this.$L('操作成功！'));
                                        this.getData();
                                    } else {
                                        this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    }
                                }, (error) => {
                                    this.$message.error(this.$L('操作失败！请稍后重试！'));
                                    this.showLoad(false);
                                });
                            }).catch(action => {

                            });
                        }
                        break;
                    case 'evaluate':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 0) {
                            this.$router.push({name: "resOrderComment", params: {id: item.orderId}});
                        }
                        break;
                    case 'evaluateInfo':
                        //去评价
                        if (item.order_status >= 3 && item.comments_status === 1) {
                            this.$router.push({name: "resCheckComment", params: {id: item.orderId}});
                        }
                        break;
                }
            },
            showLoad(type) {
                if (typeof type === "undefined" || type) {
                    this.loading = this.$loading({lock: true});
                } else {
                    if (this.loading) {
                        this.loading.close();
                        this.loading = null;
                    }
                }
            },
            getInfoFunc(id){
                this.$router.push({name:'reservationInfo',params:{id:id}});
            },
			showTimeFunc(){
				this.showTime = !this.showTime;
			},
			onChange(date) {
				let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
				arr = arr.replace(/"|\[|\]/g,'');
				arr = arr.split(',');
				this.defaultDate = arr;
				this.dtCreateStart = arr[0];
				this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
			},
			closeTimeFunc(){
				this.showTime = false;
				this.defaultDate = [];
				this.dtCreateStart = '';
				this.dtCreateEnd = '';
				this.getData();
			},
			gotoTime(){
				this.showTime = false;
				this.getData();
			},
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../../assets/css/common/lib.scss';
    @import "../../../assets/css/common/common";
    .reservation{
        position: absolute;
        width: 100%;
        height: 100%;
        line-height: 45px;
        /*background-color: #fff;*/
        top: 0;
        left: 0;
        border-bottom: 1px solid #E4E7ED;
        .showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 45px;
            }
        }
        .search {
            position: relative;
            width: 100%;
            height: 0;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            @include diy-animation-show(height);
            border-bottom: 1px solid #F2F6FC;

            &.active {
                height: 50px;
                padding: 0 10px;
                background: #fff;
            }

            .bbx-font {
                position: absolute;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                top: 0;
                right: 0;
                font-size: 20px;
            }
            .search-box{
                width: calc(100% - 40px);
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
                /deep/ .el-input-group__append{
                    padding: 0 12px;
                    background: #fff;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
        .search-status{
			padding: 0 10px;
            height: 46px;
            border-bottom: 1px solid #F2F6FC;
            background: #fff;
            /deep/ .el-select{
                margin: 0 10px;
                width: calc(35% - 20px);
                vertical-align: top;
            }
            .time-search{
                margin: 7px 0 7px 10px;
                width: calc(65% - 10px);
            }
        }
        .list-content{
            width: 100%;
            .list-main{
                margin: 10px 0;
                background: #fff;
                .item{
                    padding: 0 10px;
                    line-height: 25px;
                    .font-red{
                        color: red;
                    }
                    .font-green{
                        color: #00bf00;
                    }
                    .right{
                        width: 30%;
                        display: inline-block;
                        text-align: right;
                        overflow: hidden;
                        white-space: nowrap;
                        vertical-align: top;
                    }
                    .left{
                        width: 70%;
                        display: inline-block;
                        overflow: hidden;
                        white-space: nowrap;
                        vertical-align: top;
                        .list-main-left{
                            width: 80px;
                            height: 80px;
                            float: left;
                            .img{
                                width: inherit;
                                height: inherit;
                            }
                        }
                        .list-main-right{
                            padding-left: 10px;
                            float: left;
                            height: 100px;
                            width: calc(100% - 120px);
                            .order-info{
                                line-height: 26px;
                            }
                            .order-info{
                                cursor: pointer;
                                overflow:hidden;
                                display:-webkit-box;
                                text-overflow:ellipsis;
                                -webkit-line-clamp:2;
                                -webkit-box-orient:vertical;
                                white-space:normal;
                                font-size: 12px;
                                line-height: 1.6;
                            }
                            .price{
                                padding-top: 15px;
                            }
                        }
                    }
                    &.head{
                        padding: 10px;
                    }
                    &.item-detail{
                        height: 80px;
                    }
                    &.item-btn{
                        text-align: right;
                        padding: 10px;
                    }
                }
            }
            .page{
                background: #fff;
                padding: 20px 0;
                text-align: center;
                /deep/ .el-pagination{
                    height: 26px;
                }
            }
        }
        .reservation-content{
            margin-top: 10px;
            .list-main{
                background-color: #fff;
                padding: 15px 0;
                margin-bottom: 15px;
                .list-msg{
                    background: #fff;
                    font-size: 14px;
                    .inner-span{
                        padding: 0 40px 0 20px;
                        height: 50px;
                        line-height: 50px;
                    }
                    .order-time{
                        color: #999;
                    }
                }
                .list-info{
                    margin: 0 20px;
                    background: #fff;
                    padding: 20px 0;
                    .item {
                        display: inline-block;
                        text-align: center;
                        vertical-align: top;
                        &.item-one{
                            width: 13%;
                            height: 100px;
                            font-size: 14px;
                            .price{
                                height: 100px;
                                margin-bottom: 20px;
                                padding-top: 10px;
                                &:last-child{
                                    margin-bottom: 0;
                                    font-weight: 700;
                                }
                                .font-grey{
                                    color: #999;
                                    text-decoration: line-through;
                                }
                            }
                            .font-red{
                                color: red;
                                padding: 10px 0;
                            }
                            .font{
                                padding: 10px 0;
                            }
                            .unconfirm{
                                color: green;
                                padding: 10px 0;
                            }
                            .undeliver{
                                color: red;
                                padding: 10px 0;
                            }
                        }
                        &.item-two{
                            width: 35%;
                            text-align: left;
                        }
                        &.item-three{
                            width: 13%;
                            font-size: 14px;
                            padding: 10px 0;
                            button{
                                display: block;
                                margin: 0 auto 10px;
                                display: block;
                                width: 100px;
                                line-height: 35px;
                                padding: 0;
                                &:last-child{
                                    margin-bottom: 0;
                                }
                            }
                        }
                        .info{
                            position: relative;
                            width: 100%;
                            overflow: hidden;
                            padding-bottom: 20px;
                            &:last-child{
                                padding-bottom: 0;
                            }
                            .list-main-left{
                                width: 100px;
                                height: 100px;
                                float: left;
                                img{
                                    width: 100px;
                                    height: 100px;
                                }
                            }
                            .list-main-right{
                                padding-left: 18px;
                                float: left;
                                height: 100px;
                                width: calc(100% - 120px);
                                p{
                                    line-height: 26px;
                                }
                                .order-info{
                                    cursor: pointer;
                                    overflow:hidden;
                                    display:-webkit-box;
                                    text-overflow:ellipsis;
                                    -webkit-line-clamp:2;
                                    -webkit-box-orient:vertical;
                                    white-space:normal;
                                    font-size: 12px;
                                    line-height: 1.6;
                                }
                            }

                        }

                    }
                }

            }
            .list-pages{
                text-align: center;
                padding: 20px 0 20px;
            }
        }
        .tips{
            position: relative;
            background: #fefefe;
            padding: 20px;
            margin-top: 10px;
            text-align: center;
            .no-list{
                line-height: 60px;
                width: 50%;
                color: #909399;
                margin: 0 auto;
                font-size: 14px;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
		.time{
			height: 100%;
			position: fixed;
			z-index: 1000;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			background: rgba(0, 0, 0, 0.6);
			.date{
				width: 100%;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				.btns{
					background-color: #fff;
					text-align: right;
					padding: 5px 10px;
				}
			}
			.close{
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
			}
		}
		.fade-enter,
		.fade-leave-to{
		   opacity: 0;
		}
		.fade-enter-active,
		.fade-leave-active{
		   transition: opacity 0.5s
		}
    }

